const className = 'home-banner--promo-alt';

let prevRatio = 0.0;
let elementWatch;
let defaultTransitionElemnt;
let scale = 'scale(1.0X)';

function takeOver(box) {
    defaultTransitionElemnt = box;
    if (box.getAttribute('data-transition-watch-element')) {
        elementWatch = document.querySelector(
            box.getAttribute('data-transition-watch-element')
        );
    } else {
        elementWatch = box;
    }

    console.log(elementWatch);

    let observer;
    let options = {
        root: null,
        rootMargin: '0px',
        threshold: buildThresholdList(),
    };

    observer = new IntersectionObserver(handleIntersect, options);
    observer.observe(elementWatch);
}

function buildThresholdList() {
    let thresholds = [];
    let numSteps = 60;

    for (let i = 1.0; i <= numSteps; i++) {
        let ratio = i / numSteps;
        thresholds.push(ratio);
    }

    thresholds.push(0);
    return thresholds;
}

function handleIntersect(entries) {
    entries.forEach((entry) => {
        console.log('entry.intersectionRatio: ' + entry.intersectionRatio);
        console.log(
            'ratio is: ' + Math.round(10 - entry.intersectionRatio * 10)
        );

        if (entry.intersectionRatio > prevRatio) {
            /*
            defaultTransitionElemnt.querySelector(
                '.takeover-transition-opacity'
            ).style.opacity = entry.intersectionRatio;
            */

            defaultTransitionElemnt.querySelector(
                '.banner-transition-ele'
            ).style.transform = scale.replace(
                'X',
                Math.round(10 - entry.intersectionRatio * 10)
            );

            entry.intersectionRatio === 0
                ? (defaultTransitionElemnt.querySelector(
                      '.banner-transition-ele'
                  ).style.position = 'absolute')
                : (defaultTransitionElemnt.querySelector(
                      '.banner-transition-ele'
                  ).style.position = 'fixed');
        } else {
            defaultTransitionElemnt.querySelector(
                '.banner-transition-ele'
            ).style.transform = scale.replace(
                'X',
                Math.round(10 - entry.intersectionRatio * 10)
            );

            entry.intersectionRatio === 0
                ? (defaultTransitionElemnt.querySelector(
                      '.banner-transition-ele'
                  ).style.position = 'absolute')
                : (defaultTransitionElemnt.querySelector(
                      '.banner-transition-ele'
                  ).style.position = 'fixed');
        }

        prevRatio = entry.intersectionRatio;
    });
}

export default {
    launchFn: takeOver,
    launchQuery: `.${className}`,
};
